import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/Layout"
import PostEntry from "../components/PostEntry"
import SEO from "../components/SEO"

const CategoryTemplate = props => {
  const {
    data: {
      wpgraphql: { category },
    },
  } = props
  return (
    <Layout>
      <SEO
        title={category.name}
        description={category.name  + " Sàn bất động sản uy tín, chuyên nghiệp Alphaland"}
        keywords={category.name  + " Sàn bất động sản, nhà đất tp hcm, dự án bất động sản, chung cư tphcm, nhà đất Bình Dương, Nhà đất Đồng Nai"}
        seoImage={'https://www.sharkhung.vn/ogimage-default.jpg'}
      />
      <div className="big-title mb-5">
        <div className="container text-center">
          <h1>{category.name}</h1>
        </div>
      </div>

      <div className="category">
        <div className="container">
          <div className="list-post w-100">
            <div className="row">
              {category.posts.nodes && category.posts.nodes.map(post =>
                <PostEntry key={post.postId} post={post} />)}
            </div>
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default CategoryTemplate

export const pageQuery = graphql`
  query GET_CATEGORY($id: ID!) {
    wpgraphql {
      category(id: $id) {
        id
        name
        slug
        posts {
          nodes {
            id
          postId
          title
          content
          link
          uri
          excerpt
          featuredImage {
            altText
            sourceUrl
            imageFile {
                childImageSharp {
                    fluid(maxHeight: 600, maxWidth: 1000, quality: 100, cropFocus: CENTER) {
                        ...GatsbyImageSharpFluid_withWebp_tracedSVG
                    }
                }
              }
            }
            news_papers {
              fieldGroupName
              newsPapers {
                excerpt
                fieldGroupName
                link
                title
                image {
                  altText
                    sourceUrl
                    imageFile {
                        childImageSharp {
                            fluid(maxHeight: 800, maxWidth: 1400, quality: 100, cropFocus: CENTER) {
                                ...GatsbyImageSharpFluid_withWebp_tracedSVG
                            }
                        }
                      }
                }
              }
            }
            CustomPost {
              address
              price
              bedroom
              wc
              acreage
              location
              phone
              investor
              utility {
                title
              }
              gallery {
                  altText
                  sourceUrl
                  imageFile {
                      childImageSharp {
                          fluid(maxHeight: 800, maxWidth: 1400, quality: 100, cropFocus: CENTER) {
                              ...GatsbyImageSharpFluid_withWebp_tracedSVG
                          }
                      }
                    }
              }
          }
          }
          
        }
      }
    }
  }
`